
.AttachmentSetting {
  .attachment {
    display: block;
    width: 100%;
    height: 15em;
    margin-bottom: 0.5em;
  }

  .attachment-input {
    margin-left: 1em;
    display: flex;
    flex-direction: column;
    width: 20em;
  }

  &.-compact {
    .attachment-input {
      flex-direction: row;
      align-items: flex-end;
    }

    .attachment {
      flex: 0;
      order: 0;
      display: block;
      min-width: 4em;
      height: 4em;
      align-self: center;
      margin-bottom: 0;
    }

    .control-field {
      order: 1;
      min-width: 12em;
      margin-left: 0.5em;
    }

    .control-upload {
      order: 2;
      min-width: 12em;
      padding: 0 0.5em;
    }
  }

  .controls {
    margin-bottom: 0.5em;

    input,
    button {
      width: 100%;
    }
  }
}
